.updatePasswordContainer {
  /* width: 100vw;
  height: 100vh; */
  max-width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
   background-color: rgb(243, 241, 235);
  /* position: fixed; */
  /* margin-top: 3vmax;
  top: 0%;
  left: 0; */
}

.updatePasswordBox {
 background-color: rgb(207, 203, 166);
  width: 25vw;
  box-sizing: border-box;
  overflow: hidden;
    margin-top: 50px;
    margin-bottom: 50px;
}

.updatePasswordHeading {
  text-align: center;
  color: rgba(0, 0, 0, 0.664);
  font: 400 1.3vmax var(--Roboto-font-family);
  padding: 1.3vmax;
  border-bottom: 1px solid rgba(0, 0, 0, 0.205);
  width: 50%;
  margin: auto;
}

.updatePasswordForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  padding: 2vmax;
  justify-content: space-evenly;
  height: 70%;
  transition: all 0.5s;
}

.updatePasswordForm > div {
  display: flex;
  width: 100%;
  align-items: center;
  margin-bottom: 20px;
}

.updatePasswordForm > div > input {
  padding: 1vmax 4vmax;
  padding-right: 1vmax;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.267);
  border-radius: 4px;
  font: 300 0.9vmax cursive;
  outline: none;
}

.updatePasswordForm > div > svg {
  position: absolute;
  transform: translateX(1vmax);
  font-size: 1.6vmax;
  color: rgba(0, 0, 0, 0.623);
}

.updatePasswordBtn {
  border: none;
  background-color: rgb(125, 120, 74);
  color: rgb(24, 14, 14);
  font: 300 0.9vmax;
  font-family: var(--Roboto-font-family);
  width: 100%;
  padding: 0.8vmax;
  cursor: pointer;
  transition: all 0.5s;
  border-radius: 4px;
  outline: none;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}

.updatePasswordBtn:hover {
   background-color: rgb(70, 64, 8);
}

@media screen and (max-width: 1024px) {
  .updatePasswordForm > a {
    font-size: 1.2vmax;
  }
  .updatePasswordBox {
    width: 65vh;
    height: 60vh;
  }
}
@media screen and (max-width: 768px) {
  .updatePasswordForm > a {
    font-size: 1.5vmax;
  }

  .updatePasswordBox {
    width: 55vh;
    height: 60vh;
  }

  .updatePasswordForm > div > input {
    padding: 1.5vmax 5vmax;
    font: 300 1.7vmax cursive;
  }

  .updatePasswordForm > div > svg {
    font-size: 2.3vmax;
  }
}

@media screen and (max-width: 600px) {
  .updatePasswordContainer {
    background-color: rgb(255, 255, 255);
    margin-top: 10vmax;
  }
  .updatePasswordBox {
    width: 100vh;
    height: 65vh;
  }

  .updatePasswordForm {
    padding: 5vmax;
  }

  .updatePasswordForm > div > input {
    padding: 2.5vmax 5vmax;
    font: 300 1.7vmax cursive;
  }

  .updatePasswordForm > div > svg {
    font-size: 2.8vmax;
  }

  .updatePasswordBtn {
    font: 300 1.9vmax var(--Roboto-font-family);
    padding: 1.8vmax;
  }
}