.updateProfileContainer {
  /* width: 100vw;
  height: 100vh; */
  max-width: 100%;
  display: flex;
  justify-content: center;
  /* align-items: center;
  background-color: rgb(243, 241, 235);
  position: fixed;
  top: 0%;
  left: 0; */
}

.updateProfileBox {
  background-color: rgb(215, 213, 194);
  width: 25vw;
  height: 60vh;
  box-sizing: border-box;
  overflow: hidden;
  margin-top: 50px;
  margin-bottom: 50px;
}

.updateProfileHeading {
  text-align: center;
  color: rgba(0, 0, 0, 0.664);
  font: 400 1.3vmax var(--Roboto-font-family);
  padding: 1.3vmax;
  border-bottom: 1px solid rgba(0, 0, 0, 0.205);
  width: 50%;
  margin: auto;
}



.updateProfileForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  padding: 2vmax;
  justify-content: space-evenly;
  height: 70%;
  transition: all 0.5s;
  margin-top: 20px;
}



#updateProfileImage > img {
  width:3vmax;
  height: 3vmax;
  border-radius: 100%;
}
#updateProfileImage > input {
  display: flex;
  padding: 0%;
  margin-left: 0.9vmax;
}

#updateProfileImage > input::file-selector-button {
  cursor: pointer;
  width: 100%;
  z-index: 2;
  height: 5vh;
  border: none;
  margin: 0%;
  font: 400 0.8vmax cursive;
  transition: all 0.5s;
  padding: 0 1vmax;
  color: rgba(0, 0, 0, 0.623);
  background-color: rgb(255, 255, 255);
}

#updateProfileImage > input::file-selector-button:hover {
  background-color: rgb(235, 235, 235);
}


.updateProfileForm > div {
  display: flex;
  width: 100%;
  align-items: center;
}


.updateProfileForm > div > input {
  padding: 1vmax 4vmax;
  padding-right: 1vmax;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.267);
  border-radius: 4px;
  font: 300 0.9vmax cursive;
  outline: none;
  margin-bottom: 15px;
}


.updateProfileForm > div > svg {
  position: absolute;
  transform: translateX(1vmax);
  font-size: 1.6vmax;
  color: rgba(0, 0, 0, 0.623);
}




.updateProfileBtn {
  border: none;
 background-color: rgb(207, 203, 166);
  color: rgb(17, 12, 12);
  font: 300 0.9vmax;
  font-family: var(--Roboto-font-family);
  width: 100%;
  padding: 0.8vmax;
  cursor: pointer;
  transition: all 0.5s;
  border-radius: 4px;
  outline: none;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);


}


.updateProfileBtn:hover {
  background-color: rgb(87, 84, 83);
}


@media screen and (max-width: 1024px) {
  .updateProfileForm > a {
    font-size:1.2vmax;
  }
  .updateProfileBox {
    width: 65vh;
    height: 60vh;
  }

}
@media screen and (max-width: 768px) {
  .updateProfileForm > a {
    font-size:1.5vmax;
  }

  .updateProfileBox {
    width: 55vh;
    height: 60vh;
    margin-top:40px
  }
 
  .updateProfileForm > div > input {
    padding: 1.5vmax 5vmax;
    font: 300 1.7vmax cursive;
  }
  
  .updateProfileForm > div > svg {
    font-size: 2.3vmax;
  }
  #updateProfileImage > img {
    width: 5vmax;
    height: 5vmax;
    border-radius: 100%;
  }
  #updateProfileImage > input::file-selector-button {
    height: 5vh;
    font: 400 1.8vmax cursive;
  }
 
  

}

@media screen and (max-width: 600px) {
  .updateProfileContainer {
     background-color: rgb(243, 241, 235);
    margin-top: 10vmax;
    
  }
  .updateProfileBox {
    width: 100vh;
    height: 95vh;
  margin-top: 20vmax;
    
  }

  
  .updateProfileForm {
    padding: 5vmax;
  }
 
  .updateProfileForm > div > input {
    padding: 2.5vmax 5vmax;
    font: 300 1.7vmax cursive;
  }

  .updateProfileForm > div > svg {
    font-size: 2.8vmax;
  }

  #updateProfileImage > img {
    width: 8vmax;
    height: 8vmax;
    border-radius: 100%;
  }

  #updateProfileImage > input::file-selector-button {
    height: 7vh;
    font: 400 1.8vmax cursive;
  }

  .updateProfileBtn {
    font: 300 1.9vmax var(--Roboto-font-family);
    padding: 1.8vmax;
  }
}