.forgotPasswordContainer {
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(231, 231, 231);
    position: fixed;
    margin-top: 3vmax;
    top: 0%;
    left: 0;
  }
  
  .forgotPasswordBox {
    background-color:rgb(217, 214, 196);
    width: 25vw;
    height: 40vh;
    box-sizing: border-box;
    overflow: hidden;
  }

  .forgotPasswordHeading {
    text-align: center;
    color: rgba(0, 0, 0, 0.664);
    font: 500 1.3vmax var(--Roboto-font-family);
    padding: 1.3vmax;
    border-bottom: 1px solid rgba(0, 0, 0, 0.205);
    width: 50%;
    margin: auto;
  }
  
  .forgotPasswordForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 2vmax;
    justify-content: space-evenly;
    height: 70%;
    transition: all 0.5s;
  }

  .forgotPasswordForm > div {
    display: flex;
    width: 100%;
    align-items: center;
  }
  

  .forgotPasswordForm > div > input {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 0.9vmax cursive;
    outline: none;
  }
  
 
  .forgotPasswordForm > div > svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.623);
  }
  

  

  .forgotPasswordBtn {
    border: none;
    background-color:rgb(127, 87, 11);
    color: white;
    font: 300 0.9vmax;
    font-family: var(--Roboto-font-family);
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
  }
  
 
  .forgotPasswordBtn:hover {
    background-color: rgb(56, 38, 4);
  }
  
  
  @media screen and (max-width: 1024px) {
    .forgotPasswordForm > a {
      font-size:1.2vmax;
    }
    .forgotPasswordBox {
      width: 65vh;
      height: 80vh;
    }
  
  }
  @media screen and (max-width: 768px) {
    .forgotPasswordForm > a {
      font-size:1.5vmax;
    }
  
    .forgotPasswordBox {
      width: 55vh;
      height: 80vh;
    }
   
    .forgotPasswordForm > div > input {
      padding: 1.5vmax 5vmax;
      font: 300 1.7vmax cursive;
    }
    
    .forgotPasswordForm > div > svg {
      font-size: 2.3vmax;
    } 
  }
  
  @media screen and (max-width: 600px) {
    .forgotPasswordContainer {
      background-color: rgb(255, 255, 255);
      margin-top: 10vmax;
      
    }
    .forgotPasswordBox {
      width: 100vh;
      height: 95vh;
    }
  
    
    .forgotPasswordForm {
      padding: 5vmax;
    }
   
    .forgotPasswordForm > div > input {
      padding: 2.5vmax 5vmax;
      font: 300 1.7vmax cursive;
    }
  
    .forgotPasswordForm > div > svg {
      font-size: 2.8vmax;
    }
 
   .forgotPasswordBtn {
      font: 300 1.9vmax var(--Roboto-font-family);
      padding: 1.8vmax;
    }
  }
  